/*
 * Top level element holding the image and selectors.
 */

.imageAndSelectors {
  display: -ms-flexbox;
  display: flex;
  height: 60vh;

  border-radius: 1rem;
  border: 1px solid lightgrey;
  padding: 1rem 2rem;
}

/*
 * Calculations display
 */

.calculatedResistanceDisplay {
  font-size: 1.5rem;
  font-family: 'Montserrat', sans-serif;

  margin: 1rem;
  padding: 0.5rem;

  border-radius: 1rem;
  border: 1px solid lightgrey;

  white-space: nowrap;
}

.calculatedResistanceDisplay > div {
  margin: 0.5rem;

  min-width: 14em;
}

.caption {
  font-size: 0.75rem;
  margin-top: 0.5rem;
  border-top: 1px solid black;
}

/*
 * Resistor image
 */

/* Make the resistor image respond to size of surroundings. It needs height or
 * width explicitly set on the parent in absolute terms, or an unbroken chain
 * of parents each setting those properties as percentages until the final
 * parent sets it in concrete terms. */
.resistorImageWrapper, .resistorImageWrapper > div {
  height: 100%;
  width: 100%;
}

svg {
  height: 100%;
  width: 100%;
}


/*
 * Selectors menus
 */

.selectors {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;

  padding-left: 1rem;
}

.selectors > select {
  font-size: 2rem;
  font-family: 'Maven Pro', sans-serif;
  padding: 0.2rem;
  border-radius: 0.5rem;
  text-transform: uppercase;

  min-width: 4em;
}

.selectors > select:focus {
  -webkit-transition: 0.5s, -webkit-box-shadow 0.1s;
  transition: 0.5s, -webkit-box-shadow 0.1s;
  -o-transition: 0.5s, box-shadow 0.1s;
  transition: 0.5s, box-shadow 0.1s;
  transition: 0.5s, box-shadow 0.1s, -webkit-box-shadow 0.1s;
  -webkit-box-shadow: 0 0 11px 4px #d9bb7a;
          box-shadow: 0 0 11px 4px #d9bb7a;
  outline: none;
}

#bandA, #bandB, #bandC, #bandD {
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}



